h1 {
  font-weight: 300;
  font-size: 3rem;
}

h2 {
  @include rtp-gradient;

  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 42px;
  display: inline-block;
  font-weight: 600;

  .rtp-history & {
    -webkit-text-fill-color: initial;
  }
}

h3 {
  font-weight: 500;
  font-size: 22px;
  line-height: 1.3em;
}

p {
  font-size: 18px;
}



